<template>
    <div id="login">
        <div>
            <el-form label-width="60px" :rules="rules" :model="loginForm" ref="loginForm" class="loginForm" :label-position="labelPosition">
                <h3 class="loginTitle"></h3>
                <el-form-item prop="username" label="">
                    <el-input type="text" v-model="loginForm.username" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
                <el-form-item prop="password" label="">
                    <el-input type="password" v-model="loginForm.password" @keydown.enter.native="submitForm" auto-complete="off" placeholder=""></el-input>
                </el-form-item>
                <el-checkbox v-model="checked" style="margin-bottom: 10px"></el-checkbox>
                <el-button style="width: 100%" type="primary"  @click="submitForm"></el-button>
                <div class="tip">
                    : IE 11
                </div>
            </el-form>
        </div>
    </div>
</template>

<script>

    export default {
        name: "Login",
        data() {
            return {
                labelPosition: 'right',
                loginForm: {
                    username: 'admin',
                    password: '123'
                },
                checked:false,
                rules: {
                    username: [{required: true, message: "", trigger: 'blur'}],
                    password: [{required: true, message: "", trigger: 'blur'}],
                },
            }
        },
        methods:{
            submitForm(){
                this.$router.push('/home');
                this.$refs.loginForm.validate((valid) => {
                    if (valid) {
                        this.postRequest('/monitor/doLogin',this.loginForm).then(resp=>{ //resp api.js 
                            // resp    api
                            if(resp){
                                window.sessionStorage.setItem("user",JSON.stringify(resp.obj));
                                let path = this.$route.query.redirect;
                                console.log(this.$route.query.redirect);
                                this.$router.replace((path === '/' || path === undefined) ? '/home' : path);//replace  homepush
                            }
                        })
                    } else {
                        this.$message({
                            message: '',
                            type: 'warning'
                        });
                        return false;
                    }
                });
            }
        }
    }
</script>

<style>
    #login{
        width: 100%;
        height: 100%;
        background-image: url('./images/login_bg.png') !important;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background-attachment: fixed;
        background-color: #8ea09f;
        font-size: 12px;
        color: #333;
        overflow:hidden
    }
    .loginForm{
        border-radius: 15px;
        margin: 180px auto;
        width: 400px;
        /**/
        padding: 25px 35px 35px 35px ;
        background: #fff;
        border: 1px solid #eaeaea;
        box-shadow: 0 0 25px #eaeaea;
    }
    .loginTitle{
        margin: 15px auto 15px auto;
        text-align: center;
        color: #15a06c;
    }
    .tip{
        margin-top: 10px;
    }
</style>
