<template>
    <div :id="id" :style="style"></div>
</template>

<script>
    export default {
        name: "PointAll",
        data(){
            return{
                option :{
                    title: {
                        text: '',
                    },
                    tooltip: {
                        trigger: 'axis',
                        axisPointer: {
                            type: 'shadow'
                        }
                    },
                    legend: {
                        data: ['']
                    },
                    grid: {
                        left: '3%',
                        right: '4%',
                        bottom: '3%',
                        containLabel: true
                    },
                    xAxis: {
                        type: 'value',
                        boundaryGap: [0, 0.01]
                    },
                    yAxis: {
                        type: 'category',
                        data: ['cpu', '', '', '', 'ping', 'snmp','','','ospf','TRAP']
                    },
                    series: [
                        {
                            name: '',
                            type: 'bar',
                            data: [21, 12, 120, 15,21, 12, 120, 15,30,55],
                            color: ['#f49f42'],
                        },

                    ]
                },
                chart:'',
            }
        },
        computed: {
            style() {
                return {
                    height: this.height,
                    width: this.width
                }
            }
        },
        props:{
            id: {
                type: String
            },
            ip:{
                type:String
            },
            width: {
                type: String,
                default: "100%"
            },
            height: {
                type: String,
                default: "260px"
            },
            data: Object,
        },
        methods:{
            initPoint(){
                this.chart = this.$echarts.init(document.getElementById(this.id));
                this.chart.setOption(this.option,true);
                window.addEventListener("resize", this.chart.resize);
            }
        },
        mounted() {
            this.initPoint();
        },
    }
</script>

